#DHDTextWrapper {
  height: 50vh;
  width: 80vw;
  position: absolute;
  margin-left: 10vw;
  margin-top: 20vh;
}

#DHDMain h1 {
  position: absolute;
  font-size: calc(30px + 2.3vw + 1vh);
  font-style: italic;
  font-family: 'Playfair Display', serif;
  font-weight: normal;
}

#DHDMain h2 {
  position: absolute;
  margin-top: 18vh;
  font-size: 3vh;
}

#learnBox {
  cursor: pointer;
  height: 5vh;
  width: 13vw;
  position: absolute;
  margin-top: 35vh;
  outline: 0.3vh solid white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2vh;
}
